import React, { Component } from "react";
import request from "../api/api"
import Cookie from "js-cookie"
import Pubsub from "pubsub-js"

class A extends React.Component{
  constructor(props){
    super(props)
    this.blur = this.blur.bind(this)
  }
  blur(event){
    var value = event.target.value;
    //发布者 把输入的值传给Login组件
    Pubsub.publish("value",value)
  }
  render(){
      return (
          <fieldset className="form-group">
              <input
                className="form-control form-control-lg"
                type="text"
                placeholder="Your Name"
                onBlur={this.blur}
              />
          </fieldset>
      )
  }
}

export default class Login extends Component {
  constructor(props){
    super(props)
    this.state = {
      username:"",
      email:"",
      password:""
    }
    // 为事件函数绑定this
    this.submit = this.submit.bind(this)
    this.blur1 = this.blur1.bind(this)
    this.blur2 = this.blur2.bind(this)
  }
  componentDidMount() {
    // console.log(this.props);
    // 订阅者接收A子组件传的数据
    Pubsub.subscribe("value",(msg,data) => {
      this.setState({
        username:data
      })
    })
  }
  submit(e){
    // 清除from表单的提交事件
    e.preventDefault();
    let user = {
      // 判断用户名没有数据就为空
      username:this.state.username ? this.state.username:"",
      email:this.state.email,
      password:this.state.password
    }
    request({
      method:"post",
      // 判断有用户名数据的话就是注册页面URL接口地址 否则就是登录页面URL接口地址
      url:this.state.username ? "/api/users" : "/api/users/login",
      data:{
        user:user
      }
    }).then(res => {
      // console.log(res);
      // 把token存放在 cookie中
      Cookie.set("auth",res.data.user.token)
      // 发布
      Pubsub.publish("a",res.data.user.token)
      // 路由跳转
      this.props.history.push("/")

      Pubsub.publish("logo",res.data.user)
    })
  }
  blur1(e){
    // console.log(e.target.value);
    this.setState({
      email:e.target.value
    })
  }
  blur2(e){
    // console.log(e.target.value);
    this.setState({
      password:e.target.value
    })
  }
  isLogin(){
    //判断是注册还是登陆
    return this.props.match.path === "/login"
  }
  render() {
    return (
      <div className="auth-page">
        <div className="container page">
          <div className="row">
            <div className="col-md-6 offset-md-3 col-xs-12">
              <h1 className="text-xs-center">
                  {this.isLogin() ? "Sign in" : "Sign up"}
              </h1>
              <p className="text-xs-center">
                <a href="/">
                    {this.isLogin() ? "Need an account?" : "Have an account?"}
                </a>
              </p>

              <ul className="error-messages">
                <li>That email is already taken</li>
              </ul>

              <form onSubmit={this.submit}>
                {this.isLogin() ? "" : <A/>}
                <fieldset className="form-group">
                  <input
                    className="form-control form-control-lg"
                    type="text"
                    placeholder="Email"
                    onBlur={this.blur1}
                  />
                </fieldset>
                <fieldset className="form-group">
                  <input
                    className="form-control form-control-lg"
                    type="password"
                    placeholder="Password"
                    onBlur={this.blur2}
                  />
                </fieldset>
                <button className="btn btn-lg btn-primary pull-xs-right">
                    {this.isLogin() ? "Sign in" : "Sign up"}
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
