import React, { Component } from "react";
import request from "../api/api";
import { NavLink } from "react-router-dom";
import { addFavorite,deleFavorite} from "../api/favorite"

export default class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data:{
        articles: [],
      },
      tags: [],
      pages:[], //for循环遍历出的总页数数据
      limit: 20, //每页显示的条数
      // totalPage: "", //总页数
      offset: 0, //当前页
      activePage: 0, //点击的当前页
      favoritedisabled:false, //实现数据在加载过程中点赞禁止 false可点击 true不可点击
    };
    // 事件函数绑定this
    this.click = this.click.bind(this)
    this.fnFavorite = this.fnFavorite.bind(this)
  }
  componentDidMount() {
    // 获取文章内容数据渲染到页面
    request({
      method: "GET",
      url: "/api/articles",
      params: {},
    }).then((res) => {
      // console.log("res",res);
      this.setState({
        data: res.data,
      });
    });
    // 获取标签数据渲染到页面
    request({
      methods: "GET",
      url: "/api/tags",
      params: {},
    }).then((res) => {
      // console.log(res);
      this.setState({
        tags: res.data.tags,
      });
    });
    // 获取文章内容数据实现分页
    request({
      methods: "GET",
      url: "/api/articles",
      params: {
        limit: this.state.limit,
        offset: this.state.offset,
      },
    }).then((res) => {
      // console.log(res);
      // const page = res.data.articlesCount / res.data.articles.length;
      // this.setState({
      //   totalPage: res.data.articlesCount % res.data.articles.length === 0 ? page : page + 1,
      // });
      var pages1 = []
      for(var i = 0;i < res.data.articlesCount / res.data.articles.length;i++){
        pages1.push(i)
      }
      this.setState({
        pages:pages1
      })
    });
  }
  // 点击分页切换文章内容
  click(i){
    // console.log(i);
    request({
      methods: "GET",
      url: "/api/articles",
      params: {
        limit: this.state.limit, // 不写默认20条
        offset: i * this.state.limit, //点击分页整页文章内容切换：当前页*每页显示的条数
      },
    }).then((res) => {
      this.setState({
        data:res.data,
        activePage:i
      })
    })
  }
  // 实现点赞
  async fnFavorite(data){
    //数据在加载过程中点赞禁止  favoritedisabled赋值为true不可点击
    await this.setState({ //异步
      favoritedisabled : true
    })

    // console.log(data); //点击的单个文章
    if(data.favorited){
      // 取消点赞的业务
      await deleFavorite(data).then((res) => { //异步
        // console.log("取消",res);
      })

      data.favorited = false; //取消点赞 favorited赋值为false
      data.favoritesCount += -1; //favoritesCount点赞数减一

      let key = this.state.data.articles.indexOf(data) //获取当前点击单个文章在此页的索引
      // console.log("key",key);
      this.state.data.articles[key] = data //把点击之后新文章内容赋值给根据索引找到的旧文章内容
      await this.setState({ //更新状态刷新render()
        articles: this.state.data.articles
      })
    }else{
      // 添加点赞的业务
      await addFavorite(data).then((res) => { //异步
        // console.log("添加",res);
      })

      data.favorited = true; //添加点赞 favorited赋值为true
      data.favoritesCount += 1; //favoritesCount点赞数加一
      
      let key = this.state.data.articles.indexOf(data) //获取当前点击单个文章在此页的索引
      // console.log("key",key);
      this.state.data.articles[key] = data //把点击之后新文章内容赋值给根据索引找到的旧文章内容
      await this.setState({ //更新状态刷新render()
        articles: this.state.data.articles
      })
    }

    //数据加载完成  favoritedisabled赋值为false可点击
    await this.setState({ //异步
      favoritedisabled : false
    })
  }
  render() {
    return (
      <div className="home-page">
        <div className="banner">
          <div className="container">
            <h1 className="logo-font">conduit</h1>
            <p>A place to share your knowledge.</p>
          </div>
        </div>

        <div className="container page">
          <div className="row">
            <div className="col-md-9">
              <div className="feed-toggle">
                <ul className="nav nav-pills outline-active">
                  <li className="nav-item">
                    <a className="nav-link disabled" href="/">
                      Your Feed
                    </a>
                  </li>
                  <li className="nav-item">
                    <a className="nav-link active" href="/">
                      Global Feed
                    </a>
                  </li>
                </ul>
              </div>
              {this.state.data.articles.map((item, index) => {
                return (
                  <div className="article-preview" key={index}>
                    <div className="article-meta">
                      <a href="profile.html">
                        <img src={item.author.image} alt="" />
                      </a>
                      <div className="info">
                        <a href="/" className="author">
                          {item.author.username}
                        </a>
                        <span className="date">{item.createdAt}</span>
                      </div>
                      <button className="btn btn-outline-primary btn-sm pull-xs-right"
                        onClick={() => {
                          this.fnFavorite(item)
                        }}
                        disabled = {this.state.favoritedisabled}
                      >
                        <i className="ion-heart"></i> {item.favoritesCount}
                      </button>
                    </div>
                    <a href="/" className="preview-link">
                      <h1>{item.title}</h1>
                      <p>{item.description}</p>
                      <span>Read more...</span>
                    </a>
                  </div>
                );
              })}
              <ul className="pagination">
                {/* {Array.from(Array(this.state.totalPage), (e, i) => {
                  return (
                    <li
                      className={this.state.active === i ? "page-item ng-scope active" : "page-item ng-scope"}
                      key={i}
                      onClick={() => {
                        this.click(i);
                      }}
                    >
                      <NavLink className="page-link ng-binding" to="/">
                        {i + 1}
                      </NavLink>
                    </li>
                  );
                })} */}
                {this.state.pages.map((e,i) => {
                  return (
                    <li 
                      className={this.state.activePage === i ? "page-item ng-scope active" : "page-item ng-scope"}
                      key={i} 
                      onClick={()=>{
                        this.click(i)
                      }}
                    >
                      <NavLink className="page-link ng-binding" to="/">{i+1}</NavLink>
                    </li>
                  )
                })}
              </ul>
            </div>

            <div className="col-md-3">
              <div className="sidebar">
                <p>Popular Tags</p>
                <div className="tag-list">
                  {this.state.tags.map((item, index) => {
                    return (
                      <a href="/" key={index} className="tag-pill tag-default">
                        {item}
                      </a>
                    );
                  })}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
