import React from "react";
import { NavLink } from "react-router-dom";
import Cookie from "js-cookie"
import Pubsub from "pubsub-js"
import "../main.css";

class Header extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      auth: "",
      user:""
    }
  }
  componentDidMount(){
    var that = this
    //将Cookie中的a  和   state中的auth 进行合并
    Pubsub.subscribe("a",function(msg,data) {
      // console.log(msg,data);
      that.setState({
        auth:data
      })
    })
    Pubsub.subscribe("logo",function(msg,data) {
      console.log(msg,data);
      that.setState({
        user:data
      })
    })
  }
  render() {
    if(Cookie.get("auth")){
      return (
      <div>
        <nav className="navbar navbar-light">
          <div className="container">
            <a className="navbar-brand" href="index.html">
              conduit
            </a>
            <ul className="nav navbar-nav pull-xs-right">
              <li className="nav-item">
                <NavLink className="nav-link active" to="/">
                  Home
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to="/editor">
                  <i className="ion-compose"></i>&nbsp;New Article
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link" to="/settings">
                  <i className="ion-gear-a"></i>&nbsp;Settings
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink className="nav-link ng-binding" ui-sref-active="active" ui-sref="app.profile.main({ username: $ctrl.currentUser.username })" to="/profile">
                  <img className="user-pic" src="https://z3.ax1x.com/2021/04/28/gPEQsS.jpg" alt=""/>
                  zwq
                </NavLink>
              </li>
            </ul>
          </div>
        </nav> 
      </div>
    );
    }else{
      return (
        <div>
          <nav className="navbar navbar-light">
            <div className="container">
              <a className="navbar-brand" href="index.html">
                conduit
              </a>
              <ul className="nav navbar-nav pull-xs-right">
                <li className="nav-item">
                  <NavLink className="nav-link active" to="/">
                    Home
                  </NavLink>
                </li>
                <li className="nav-item">
                    <NavLink className="nav-link" to="/login">
                      Sign in
                    </NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink className="nav-link" to="/register">
                      Sign up
                    </NavLink>
                  </li>
              </ul>
            </div>
          </nav>
        </div>
      );
    }
      
  }
}
export default Header;
